@charset "UTF-8";
/*
==========================================================
                  GENERAL DEFINITIONS
==========================================================
*/
[layout] {
  display: flex; }

[layout*="row"],
[layout*="column"] {
  width: 100%;
  max-width: 100%; }

[layout~="row"] {
  flex-direction: row; }

[layout~="column"] {
  flex-direction: column; }

[layout*="row"][layout*="reverse"] {
  flex-direction: row-reverse; }

[layout*="column"][layout*="reverse"] {
  flex-direction: column-reverse; }

[layout*="rows"],
[layout*="columns"] {
  flex-wrap: wrap; }

[layout="none"] {
  flex: none; }

/*
==========================================================
                  MAIN AXIS ALIGNMENT
==========================================================
*/
[layout*="column"][layout*="top-"],
[layout*="row"][layout*="-left"] {
  justify-content: flex-start; }

[layout~="centered"],
[layout*="column"][layout*="center-"],
[layout*="row"][layout*="-center"] {
  justify-content: center; }

[layout*="column"][layout*="bottom-"],
[layout*="row"][layout*="-right"] {
  justify-content: flex-end; }

[layout*="column"][layout*="spread-"],
[layout*="row"][layout*="-spread"] {
  justify-content: space-around; }

[layout*="column"][layout*="justify-"],
[layout*="row"][layout*="-justify"] {
  justify-content: space-between; }

/*
==========================================================
                CROSS AXIS ALIGNMENT
==========================================================
*/
[layout*="column"][layout*="-left"],
[layout*="row"][layout*="top-"] {
  align-items: flex-start; }

[layout~="centered"],
[layout*="column"][layout*="-center"],
[layout*="row"][layout*="center-"] {
  align-items: center; }

[layout*="column"][layout*="-right"],
[layout*="row"][layout*="bottom-"] {
  align-items: flex-end; }

[layout*="column"][layout*="-stretch"],
[layout*="row"][layout*="stretch-"] {
  align-items: stretch; }

/*
==========================================================
               MULTI-LINE ALIGNMENT
==========================================================
*/
[layout*="rows"][layout*="top-"],
[layout*="columns"][layout*="-left"] {
  align-content: flex-start; }

[layout*="rows"][layout*="bottom-"],
[layout*="columns"][layout*="-right"] {
  align-content: flex-end; }

[layout*="rows"][layout*="center-"],
[layout*="columns"][layout*="-center"] {
  align-content: center; }

[layout*="rows"][layout*="justify-"],
[layout*="columns"][layout*="-justify"] {
  align-content: space-between; }

[layout*="rows"][layout*="spread-"],
[layout*="columns"][layout*="-spread"] {
  align-content: space-around; }

[layout*="rows"][layout*="stretch-"],
[layout*="columns"][layout*="-stretch"] {
  align-content: stretch; }

/*
==========================================================
                       IE10+ HACKS
==========================================================
*/
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  [layout*="column"]:not([layout*="row"]) > * {
    max-width: 100%; }
  [layout*="column"][self*="top"] {
    height: auto !important; }
  [self~="size-"] > * {
    height: auto; } }

/*
==========================================================
                    SELF ALIGNMENT
==========================================================
*/
[layout*="column"]:not([layout*="row"]) [self*="left"],
[layout*="row"]:not([layout*="column"]) [self*="top"] {
  align-self: flex-start; }

[self~="center"] {
  align-self: center; }

[layout*="column"]:not([layout*="row"]) [self*="right"],
[layout*="row"]:not([layout*="column"]) [self*="bottom"] {
  align-self: flex-end; }

[self*="stretch"] {
  align-self: stretch; }

/* ALIGN VIA MARGINS
========================================================== */
/* Container: */
[layout][self*="center"] {
  margin-left: auto;
  margin-right: auto; }

[layout][self*="right"] {
  margin-right: 0; }

[layout][self*="left"] {
  margin-left: 0; }

/* Items: */
[layout*="column"] [self*="bottom"] {
  margin-top: auto; }

[layout*="column"] [self*="top"] {
  margin-bottom: auto; }

[layout*="row"] [self*="left"] {
  margin-right: auto; }

[layout*="row"] [self*="right"] {
  margin-left: auto; }

/*
==========================================================
                      SELF SIZE
==========================================================
*/
/* Relative size */
[self~="size-1of5"] {
  width: 20%; }

[self~="size-1of4"] {
  width: 25%; }

[self~="size-1of3"] {
  width: 33.33333%; }

[self~="size-2of5"] {
  width: 40%; }

[self~="size-1of2"] {
  width: 50%; }

[self~="size-3of5"] {
  width: 60%; }

[self~="size-2of3"] {
  width: 66.6666%; }

[self~="size-3of4"] {
  width: 75%; }

[self~="size-4of5"] {
  width: 80%; }

[self~="size-1of1"] {
  width: 100%; }

/* Proportional size*/
[layout*="row"][layout*="-stretch"] > *:not([self*="size-"]),
[layout*="column"][layout*="stretch-"] > *:not([self*="size-"]),
[self~="size-x1"] {
  flex: 1 0 0 !important; }

[self~="size-x2"] {
  flex: 2 0 0 !important; }

[self~="size-x3"] {
  flex: 3 0 0 !important; }

[self~="size-x4"] {
  flex: 4 0 0 !important; }

[self~="size-x5"] {
  flex: 5 0 0 !important; }

[self~="size-x6"] {
  flex: 6 0 0 !important; }

[self~="size-x7"] {
  flex: 7 0 0 !important; }

[self~="size-x8"] {
  flex: 8 0 0 !important; }

[self~="size-x9"] {
  flex: 9 0 0 !important; }

[self*="size-auto"] {
  flex: 1 1 auto; }

[self*="size-x0"] {
  flex: 0 0 auto; }

/* Fixed max-width size */
[self~="size-xxlarge"] {
  max-width: 1440px;
  width: 100%; }

[self~="size-xlarge"] {
  max-width: 1200px;
  width: 100%; }

[self~="size-large"] {
  max-width: 960px;
  width: 100%; }

[self~="size-larger"] {
  max-width: 840px;
  width: 100%; }

[self~="size-medium"] {
  max-width: 720px;
  width: 100%; }

[self~="size-smaller"] {
  max-width: 600px;
  width: 100%; }

[self~="size-small"] {
  max-width: 480px;
  width: 100%; }

[self~="size-xsmall"] {
  max-width: 360px;
  width: 100%; }

[self~="size-xxsmall"] {
  max-width: 240px;
  width: 100%; }

[self*="size-x"]:not([self*="small"]):not([self*="large"]) {
  flex-shrink: 1; }

[self~="order-first"] {
  order: -1; }

[self~="order-1"] {
  order: 1; }

[self~="order-2"] {
  order: 2; }

[self~="order-3"] {
  order: 3; }

[self~="order-last"] {
  order: 999; }

[layout*="column"]:not([layout*="row"]) > * {
  flex-shrink: 0;
  flex-basis: auto; }

/* Optional additional features â€” (un)comment to include/exclude */
@media screen and (max-width: 64em) {
  [layout*="lg-row"] {
    flex-direction: row; }
  [layout*="lg-column"] {
    flex-direction: column; } }

@media screen and (max-width: 52em) {
  [layout*="md-row"] {
    flex-direction: row; }
  [layout*="md-column"] {
    flex-direction: column; }
  [layout*="md-rows"],
  [layout*="md-columns"] {
    flex-wrap: wrap; } }

@media screen and (max-width: 40em) {
  [layout*="sm-row"] {
    flex-direction: row; }
  [layout*="sm-column"] {
    flex-direction: column; } }
